  transition: transform 0.5s ease-in-out, box-shadow 0.5s ease-in-out;
}
.context-btn:before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  display: inline-block;
  width: 100%;
  height: 100%;
  border-radius: 114px;
  background-color: rgba(157, 200, 240, 0.3);
  transition: left 0.5s ease-in-out, transform 0.5s ease-in-out;
}
