  box-shadow: 5px 10px 50px 8px rgba(157, 200, 240, 0.3), inset 0 0 10px 5px rgba(157, 200, 240, 0.3);
}
.context-btn:hover::before {
  left: 0;
  transition: left 0.5s ease-in-out;
}

