  overflow: hidden;
}
.slider-item:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(245, 245, 245, 0.8235294118);
  backdrop-filter: blur(2px);
  transition: opacity 1.5s;
}

.slick-center {
  background-color: #00BDED;
  transform: scale(1.4);
  transition: transform 1.5s, opacity 1.5s, color 1.5s;
  color: #fff;
}
.slick-center:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(245, 245, 245, 0.8235294118);
