.header {
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  box-shadow: 4px 0px 6px 6px rgba(255, 255, 255, 0.7);
  background-color: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(5px);
  overflow: visible;
}

.header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__menu {
  position: relative;
}

.header__menu-list {
  display: flex;
  column-gap: 60px;
  font-size: 18px;
  font-weight: 500;
}
.header__menu-list > li {
  position: relative;
}
@media (max-width: 1440px) {
  .header__menu-list {
    column-gap: 40px;
    font-size: 16px;
  }
}

.header__drop-menu::before {
  content: "";
  position: absolute;
  top: 6px;
  right: -15px;
  display: inline-block;
  width: 15px;
  height: 15px;
  background-image: url("/img/icons/drop-menu-arrow.svg");
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  transition: transform 0.5s;
}
@media (max-width: 1440px) {
  .header__drop-menu::before {
    width: 8px;
    height: 8px;
  }
}

.header__drop-menu {
  cursor: pointer;
}

.header__drop-menu a {
  display: block;
}
.header__drop-menu li {
  position: absolute;
  left: -5px;
  top: 0;
  padding: 4px;
  width: 200px;
  box-shadow: 0px 0px 6px 6px rgb(255, 255, 255);
  background-color: rgb(255, 255, 255);
  backdrop-filter: blur(5px);
  visibility: hidden;
  opacity: 0;
}
.header__drop-menu li:first-child {
  transition: top 0.5s 0.4s, visibility 0.5s 0.4s, opacity 0.5s 0.4s;
}
.header__drop-menu li:nth-child(2) {
  transition: top 0.5s 0.2s, visibility 0.5s 0.2s, opacity 0.5s 0.2s;
}
.header__drop-menu li:nth-child(3) {
  transition: top 0.5s, visibility 0.5s, opacity 0.5s;
}

.header__drop-menu.active li {
  visibility: visible;
  opacity: 1;
}
.header__drop-menu.active li:first-child {
  top: 60px;
  transition: visibility 0.5s, opacity 0.5s, top 0.5s;
}
.header__drop-menu.active li:nth-child(2) {
  top: 100px;
  transition: visibility 0.5s 0.2s, opacity 0.5s 0.2s, top 0.5s 0.2s;
}
.header__drop-menu.active li:nth-child(3) {
  top: 140px;
  transition: visibility 0.5s 0.4s, opacity 0.5s 0.4s, top 0.5s 0.4s;
}
.header__drop-menu.active:before {
  transform: rotate(-180deg);
}

.preview {
  padding-top: 200px;
